<template>
  <div class="Favorites">
    <van-nav-bar title="我的收藏" left-arrow @click-left="back" />
    <div v-if="this.$store.state.favorites.Favorites == 0">
      <div class="Emptystate clearfix">
        <div class="picture">
          <img
            class="auto-img"
            src="../assets/images/favorites.png"
            alt
          />
        </div>
        <div class="nav clearfix">哎呀~收藏宝贝暂时没有优惠哦</div>
        <div class="title clearfix">去看看哪些宝贝值得入手吧</div>
      </div>
      <Recommend />
    </div>
    <div
      v-else
      class="likeslist fl"
      v-for="(item,index) in this.$store.state.favorites.Favorites"
      :key="index"
    >
      <div class="image" @click="goDetail(index)">
        <img
          v-if="item.goods_small_logo == 0 "
          class="auto-img"
          :src="require('../assets/images/kzt.png')"
          alt
          srcset
        />

        <img v-else class="auto-img" :src="item.goods_small_logo" alt srcset />
      </div>
      <div class="name">{{item.goods_name}}</div>
      <div class="bottom clearfix">
        <div class="price fl">
          $
          <span v-if="item.goods_price == 0">9999</span>
          <span v-else>{{item.goods_price}}</span>
        </div>
        <div class="delete fr">
          <van-icon name="delete" size="17px" @click="detail(index)" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Recommend from "../components/Recommend";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("favorites");
export default {
  data() {
    return {};
  },
  created() {
    this.FavoritesData();
    this.$store.state.favorites.Favorites = JSON.parse(localStorage.getItem("Favorites"));
  },

  methods: {
    FavoritesData() {
        let b = JSON.parse(localStorage.getItem("Favorites"));
        for (let i = b.length - 1; i >= 0; i--) {
          if (b[i].sc_isActive) {
            b.splice(i, 1);
            localStorage.setItem("Favorites", JSON.stringify(b));
          }
        }
      
      this.$store.state.favorites.Favorites = b;
    },
    //返回上一级
    back() {
      this.$router.go(-1);
    },
    //收藏删除
    detail(index) {
      this.$store.commit("favorites/FavoritesDetail", index);
    },
    // 获取商品id
    goDetail(index) {
      let goods_id = this.$store.state.favorites.Favorites[index].goods_id;
      let keywords = this.$store.state.favorites.Favorites;
      this.$router.push({ name: "Detail", query: { goods_id, keywords } });
    },
    
  },
  components: {
    ...mapState(["Favorites"]),
    Recommend,
  },
};
</script>

<style lang="less" scoped>
.Favorites {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow-y: auto;
  text-align: center;
  background-color: #f2f2f2;
  /deep/ .van-nav-bar__title {
    font-size: 18px;
  }
  /deep/.van-nav-bar__left {
    /deep/.van-nav-bar__arrow {
      font-size: 20px;
      color: black;
    }
  }
  .picture {
    position: absolute;
    top: 100px;
    left: 30%;
    width: 140px;
    height: 140px;
  }
  .nav {
    margin-top: 220px;
    font-size: 16px;
    color: rgb(102, 102, 102);
  }
  .title {
    font-size: 12px;
    margin-top: 6px;
    color: rgb(153, 153, 153);
    margin-bottom: 100px;
  }
  .likeslist {
    width: 33.4%;
    margin: 3%;
    padding: 5%;
    height: 180px;
    background-color: #fff;
    // border: 1px solid black;
    box-shadow: 4px 4px 4px #cec3c3;
    .image {
      width: 100%;
      height: 100px;
    }
    .name {
      font-weight: 600;
      margin-top: 25px;
      text-align: left;
      width: 100%;
      height: 38px;
      font-size: 14px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
    }
    .bottom {
      margin-top: 6px;
      width: 100%;
      height: 30px;
      line-height: 30px;
      .price {
        width: 80%;
        font-size: 15px;
        color: #eb5211;
        font-weight: 600;
        text-align: left;
        span {
          font-size: 19px;
        }
      }
      .delete {
        width: 20%;
        line-height: 40px;
        text-align: right;
      }
    }
  }
}
</style>